@import url(https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a{
  text-decoration: none;
  color: unset;
}

.Footer{
    margin-top: 1rem;
    border-top: 1px solid black;
    padding: 1rem;
}

.Footer .group-credit{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
}

.Footer a{
    color: rgb(54, 106, 148);
    font-weight: 500;
    text-decoration: underline;
}

.Footer a:hover{
    color: rgb(117, 179, 230);
    transition: all .2s ease-in-out;
}

.Footer h3{
    text-align: center;
    margin-bottom: 1rem;
}


.Navbar{
    border-bottom: 1px solid black;
}

.nav{
    padding: 1.2rem;
    display: flex;
    justify-content: space-between;
    max-width: 1280px;
    margin: 0 auto;
}

.nav h2{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.nav i{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.5rem;
}

.nav i:hover{
    color: #666;
    transition: all .2s ease-in-out;
}
.Tab{
    margin: 0 auto;
    max-width: 1024px;
    margin-top: 1rem;
}

.block-tabs{
    font-family: 'Kanit', sans-serif;
    border-bottom: 1px solid #666;
    display: inline-flex;
    font-size: 1.5rem;
}

.tabs{
    border: none;
    padding: 0 1rem;
    color: #777;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.tabs.active{
    border-bottom: 3px solid black;
    pointer-events: none;
    color: black;
}

.tabs:hover{
    cursor: pointer;
    color: #444;
    transition: all .3s ease-in-out;
}

.tabs.tabs.active:hover{
    cursor: default;
}

.content-tabs{
    margin-top: 1rem;
}

.content{
    display: none;
}

.content.active{
    display: block;
}


.WorldWideCovidBoard{
    padding: 1rem;
    border-radius: 10px;
    width: 100%;
    border: 1px solid black
}

.WorldWideCovidBoard .label{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main-group{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
}

.main-group .group.n1{
    border: 1px solid black;
    border-radius: 10px;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: center;
}

.main-group .group.n1 span{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
}

.main-group .group.n1 span .icon{
    width: 3rem;
    height: 3rem;
}

.main-group .group.n2 span .icon{
    width: 1.5rem;
    height: 1.5rem;
}

.main-group .group span .text{
    display: flex;
    flex-direction: column;
}

.main-group .group .span-inline{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main-group .group.n2{
    border: 1px solid black;
    border-radius: 10px;
    padding: 1rem;
}

.main-group .group.n2 .label-group{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.main-group .group.n2 .label{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: center;
    align-items: center;
}

.main-group .group.n2 span{
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
}

.ChartWorldWideCovid{
    padding: 1rem;
    border: 1px solid black;
    border-radius: 10px;
    margin-top: 1rem;
    position: relative;
    width: 100%;
}

.retrospect-world-wide{
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    padding: .5rem 1rem;
    border: 1px solid black;
}

.main-group .group.n2 .grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.chart-pie{
    /* border: 1px solid black; */
    /* margin-top: 1rem; */
    border-radius: 10px;
    padding: 1rem;
    max-width: 200px;
}

.popution{
    border-bottom: 1px solid black;
    padding-bottom: 1rem;
}
.ChooseCountry{
    font-family: 'Kanit', sans-serif;
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    border: 1px solid black;
    border-radius: 10px;
    padding: 1rem;
}

.CountryCovidBoard{
    margin-top: 1rem;
    border: 1px solid black;
    border-radius: 10px;
    padding: 1rem;
}

.CountryCovidBoard .label{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.label-choose-country{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
}

.flag-img{
    display: flex;
    justify-content: center;
    align-items: center;
}

.flag-img img{
    width: 64px; 
    height: 40px; 
    object-fit: none;
    border: 1px solid #999;
}

.flag-img.hide{
    display: none;
}

.cp-complete{
    width: 100%;
}

.autocomplete{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.ChartCountry{
    padding: 1rem;
    border: 1px solid black;
    border-radius: 10px;
    margin-top: 1rem;
    position: relative;
    width: 100%;
}

.retrospect-country{
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    padding: .5rem 1rem;
    border: 1px solid black;
}
